@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

*{
  margin: 0 auto;
  font-family: "Quicksand";
  color: #333;
  padding:3px;
  align-items: justify ;
}
.navbar{
  align-items: center ; 
  display: flex;
   margin: 0;
  padding: 20px;
  max-width: 100%;
  
  border-bottom: 1px solid #f2f2f2;
}
.navbar h1 {
  color: #f1356d;
}
.navbar .links {
  margin-left: auto;
}
.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}
.navbar a:hover {
  color:#f1356d;
}
.content{
  max-width: 600px;
  margin: 40px auto;
  padding: 20px;
  text-align: left;
  
}
.blogpreview{

  margin:0 auto;
  padding:20px;
  border-bottom: 1px solid #fafafa;
}
.blogpreview h2 {
  color: #f1356d;
  margin-bottom: 8px;
}
.blogpreview:hover{
  box-shadow:1px 3px 5px rgba(0,0,0,0.1);;
}
.blogpreview a{
  text-decoration:none;
}
.blog-details button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}
.create {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
.create label {
  text-align: left;
  display: block;
}
.create h2 {
  font-size: 20px;
  color: #f1356d;
  margin-bottom: 30px;
}
.create input, .create textarea, .create select {
  width: 100%;
  padding: 6px 10px;
  margin: 10px 0;
  border: 1px solid #ddd;
  box-sizing: border-box;
  display: block;
}
.create button {
  background: #f1356d;
  color: #fff;
  border: 0;
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
}